.card-event{
  position:relative;
  width: 360px;
  height:400px;
  border-radius:0px 10px 10px 0px;
  background:#f4f4f4;
  transform-style: preserve-3d;
  transform: perspective(1000px);
  margin: 20px 8%;
  box-shadow: 10px 20px 40px rgba(0, 0, 0, 0.25);
  transition: 1s;
}

.card-event:hover .img-box-events {
  transform: rotateY(-180deg);
}

.card-event:hover {
  transform: translateX(50%);
}

.details-events {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content-events {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform-style: preserve-3d;
  /* background: #000; */
  transition: 1s;
}

.img-box-events {
  /* border: 2px solid red; */
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  transform-origin: left;
  transform-style: preserve-3d;
  background: black;
  transition: 1s;
}

.img-box-events img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform-style: preserve-3d;
  backface-visibility: hidden;
}
.button-section{
   margin: 0 5px;
   border: 0;
   border-radius: 10px;
   background-color: orangered;
   padding: 5px;
   color: #f4f4f4;
   font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

@media only screen and (max-width: 1024px) {
    .card-bunch-child{
